import type {
  CopilotType,
  MyBoxType,
  AurorianSummaryType,
} from '@sinsa/schema';
import { CalcOptions } from '../types';

/**
 * ÊàëÁöÑ Box ËÉΩÂê¶ÊäÑËøô‰∏™‰Ωú‰∏ö
 * @param copilot ‰Ωú‰∏ö
 * @param myBox ÊàëÁöÑ Box
 * @returns ÊàëÁöÑ Box ËÉΩÂê¶ÊäÑËøô‰∏™‰Ωú‰∏ö
 */
export function canUseCopilot(
  myBox: MyBoxType['aurorian_summaries'],
  copilot: CopilotType,
  { disalbeAlternative }: CalcOptions,
): boolean {
  for (const aurorianInCopilot of copilot.aurorian_summaries) {
    // 0. Â¶ÇÊûú‰Ωú‰∏ö‰∏≠ÁöÑÂÖâÁÅµÊú¨Ë∫´ÊòØÂèØÊõøÊç¢ÁöÑÔºåÂàôË∑≥ËøáÊ≠§ÂÖâÁÅµÂà§Êñ≠
    if (!disalbeAlternative && aurorianInCopilot.is_replaceable) {
      continue;
    }

    const aurorianInMyBox = myBox[aurorianInCopilot.aurorian_name] as
      | AurorianSummaryType
      | undefined;

    // 1. ÂÖâÁÅµÊú¨Ë∫´‰∏çÂ≠òÂú®‰∫éÊàëÁöÑ box
    if (!aurorianInMyBox) {
      return false;
    }

    // 2. Â¶ÇÊûúÊàëÁöÑ box ÂÖâÁÅµÁ™ÅÁ†¥‰Ωé‰∫é‰Ωú‰∏öË¶ÅÊ±ÇÁ™ÅÁ†¥
    if (aurorianInMyBox.breakthrough < aurorianInCopilot.breakthrough) {
      return false;
    }
  }

  return true;
}
